  box-shadow: 2px 2px 2px black;
  font-family: "Permanent Marker", cursive;
  font-size: 1.5rem;
  margin: 0 -.25rem;
  text-transform: uppercase;
  transform: skewX(-15deg); }
  .search-btn:hover {
    box-shadow: 3px 4px 4px black;
    cursor: pointer;
    text-decoration: underline; }

.banner-title {
  font-family: "Bangers", cursive;
  font-size: 5rem;
  letter-spacing: 5px;
  text-align: center;
  text-shadow: 2px 3px 4px white; }

.smaller {
  background-color: #3211d4;
  font-size: 1.5rem;
  margin: 0;
  padding: .5rem;
  position: sticky;
  top: 0;
  z-index: 5; }

.card {
  background-color: white;
  border: 3px solid;
  min-height: 25em;
  display: flex;
  justify-content: center;
  height: 100%;
  animation: fadeIn 1s 1; }

@keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }
  .card img {
    height: 100%;
    width: 290px; }
  .card h3 {
    display: flex;
    justify-content: center;
    font-family: "Open Sans", sans-serif;
    max-width: 13.1em;
    margin: auto;
    font-size: 16px; }
  .card .image-container {
    height: 80%;
    min-width: 100%;
    position: relative; }
  .card .stan-lee-approved {
    position: absolute;
    top: 0;
    right: 0;
    width: 100px;
    height: 100px; }
  .card .popup-container {
    position: absolute;
    color: white;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    width: 100%;
    z-index: 100;
    animation: fadeIn 0.7s 1; }

@keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }
    .card .popup-container p {
      font-family: "Open Sans", sans-serif;
      font-size: 1rem;
      font-weight: 700; }
    .card .popup-container a {
      color: white;
      font-family: "Open Sans", sans-serif;
      font-size: 1rem; }
      .card .popup-container a:visited {
        color: #e0e000; }
      .card .popup-container a:hover {
        color: #c20000; }
  .card .info-container {
    height: 20%;
    display: flex;
    justify-content: space-around;
    flex-direction: column; }
  .card .button-container {
    display: flex;
    justify-content: space-around;
    padding-bottom: 1rem; }
  .card:hover {
    background-color: #c2bcbd; }
  .card button {
    border: 2px solid #c20000;
    box-shadow: 2px 2px 2px black;
    font-family: "Permanent Marker", cursive;
    font-size: 1rem;
    height: 1.7rem;
    transform: skewX(-15deg); }
    .card button:hover {
      box-shadow: 3px 4px 4px black;
